/* Hi·ªáu ·ª©ng n·ªÅn gradient */
.home-container {
    position: relative;
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    background: linear-gradient(135deg, #1e3c72, #2a5298);
    color: white;
    overflow: hidden;
}

/* Hi·ªáu ·ª©ng overlay */
.background-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.4);
    z-index: 0;
}

/* N·ªôi dung ch√≠nh */
.content {
    position: relative;
    max-width: 800px;
    padding: 20px;
    background: rgba(255, 255, 255, 0.1);
    border-radius: 10px;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.3);
    backdrop-filter: blur(10px);
    z-index: 1;
}

/* Ti√™u ƒë·ªÅ */
.welcome-title {
    font-size: 2.5rem;
    font-weight: bold;
    margin-bottom: 10px;
    text-transform: uppercase;
    color: #00aaff;
}

/* Hi·ªáu ·ª©ng hover cho ti√™u ƒë·ªÅ */
.welcome-title .highlight {
    color: #00ffcc;
    transition: color 0.3s ease-in-out;
}

.welcome-title .highlight:hover {
    color: #ffcc00;
}

/* ·∫¢nh ƒë·∫°i di·ªán */
.avatar {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    border: 4px solid #00aaff;
    margin: 15px 0;
    transition: transform 0.3s ease-in-out;
}

.avatar:hover {
    transform: scale(1.1);
}

/* Ph·∫ßn m√¥ t·∫£ */
.welcome-subtitle {
    font-size: 1.2rem;
    font-weight: 500;
    margin-bottom: 20px;
    line-height: 1.5;
}
