.certificates-container {
    position: relative;
    background: rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(10px);
    border-radius: 15px;
    padding: 30px;
    color: white;
    box-shadow: 0px 0px 15px rgba(0, 255, 204, 0.6);
  }
  
  .content {
    text-align: center;
  }
  
  .title {
    font-size: 2.5rem;
    font-weight: bold;
    color: #00ffcc;
  }
  
  .certificates-list {
    list-style: none;
    padding: 0;
    margin-top: 20px;
  }
  
  .certificates-list li {
    font-size: 1.2rem;
    padding: 15px;
    border-radius: 10px;
    background: rgba(255, 255, 255, 0.1);
    transition: 0.3s;
  }
  
  .certificates-list li:hover {
    transform: scale(1.1);
    box-shadow: 0px 0px 15px rgba(255, 204, 0, 0.8);
  }
  