/* Background Gradient ƒë·ªông */
@keyframes futuristic-bg {
    0% { background-position: 0% 50%; }
    50% { background-position: 100% 50%; }
    100% { background-position: 0% 50%; }
  }
  
  /* Container ch√≠nh */
  .education-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    min-height: 100vh;
    background: linear-gradient(-45deg, #0f0c29, #302b63, #24243e);
    background-size: 400% 400%;
    animation: futuristic-bg 10s ease infinite;
    color: white;
    padding: 20px;
    border-radius: 20px;
    box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.5);
  }
  
  /* Ti√™u ƒë·ªÅ ch√≠nh */
  .title {
    font-size: 2.5rem;
    font-weight: bold;
    color: #00aaff;
    text-shadow: 0px 0px 10px rgba(0, 170, 255, 0.8);
  }
  
  /* Danh s√°ch h·ªçc v·∫•n */
  .education-list {
    list-style: none;
    padding: 0;
    margin-top: 20px;
  }
  
  /* M·ªói m·ª•c h·ªçc v·∫•n */
  .education-item {
    background: rgba(255, 255, 255, 0.1);
    padding: 20px;
    margin: 10px 0;
    border-radius: 15px;
    backdrop-filter: blur(10px);
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.3);
    max-width: 500px;
    transition: all 0.3s ease-in-out;
  }
  
  /* Hi·ªáu ·ª©ng hover cho t·ª´ng m·ª•c */
  .education-item:hover {
    transform: translateY(-5px);
    background: rgba(0, 170, 255, 0.2);
    box-shadow: 0px 0px 20px rgba(0, 170, 255, 0.5);
  }
  
  /* Ti√™u ƒë·ªÅ t·ª´ng m·ª•c */
  .education-item h2 {
    font-size: 1.8rem;
    font-weight: bold;
    color: #ffcc00;
  }
  
  /* Hi·ªáu ·ª©ng ph√°t s√°ng ch·ªØ */
  .education-item p {
    font-size: 1.2rem;
    color: #ffffff;
    transition: color 0.3s ease-in-out;
  }
  
  .education-item p:hover {
    color: #00ffcc;
  }
  