/* Bi·∫øn m√†u ƒë·ªìng b·ªô */
:root {
  --background-color: linear-gradient(to right, #141e30, #243b55);
  --button-bg: #ff7eb3;
  --button-hover-bg: #ff4a91;
}

/* ƒê·ªãnh d·∫°ng to√†n b·ªô trang */
body {
  margin: 0;
  padding: 0;
  font-family: "Arial", sans-serif;
  background: var(--background-color);
  color: white;
  text-align: center;
  padding-top: 80px; /* ƒêi·ªÅu ch·ªânh theo chi·ªÅu cao navbar */

}

/* Header */
.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

/* Logo */
.App-logo {
  height: 100px;
  animation: App-logo-spin infinite 10s linear;
}

/* Hi·ªáu ·ª©ng xoay logo */
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* N√∫t b·∫•m hi·ªán ƒë·∫°i */
.App-button {
  background: var(--button-bg);
  border: none;
  padding: 0.75rem 1.5rem;
  font-size: 1.1rem;
  color: white;
  cursor: pointer;
  border-radius: 8px;
  margin-top: 1rem;
  transition: all 0.3s ease;
}

.App-button:hover {
  background: var(--button-hover-bg);
  transform: scale(1.05);
}

/* ƒê·∫£m b·∫£o n·ªôi dung kh√¥ng b·ªã che khu·∫•t */
.main-content {
  padding-top: 4rem;
  padding-bottom: 1rem;
}
