/* üåü Container ch√≠nh */
.career-goals-container {
    max-width: 900px;
    margin: 50px auto;
    padding: 20px;
    background: rgba(255, 255, 255, 0.1);
    border-radius: 12px;
    box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.3);
    text-align: center;
    color: white;
    backdrop-filter: blur(12px);
    animation: fadeIn 0.8s ease-in-out;
  }
  
  /* üåü Ti√™u ƒë·ªÅ */
  .career-goals-title {
    font-size: 30px;
    font-weight: bold;
    margin-bottom: 15px;
    color: #00aaff;
  }
  
  /* üåü ·∫¢nh minh h·ªça */
  .career-goals-image {
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
  }
  
  .career-goals-image img {
    width: 60%;
    max-width: 300px;
    border-radius: 10px;
    box-shadow: 0px 4px 10px rgba(0, 170, 255, 0.4);
  }
  
  /* üåü M√¥ t·∫£ */
  .career-goals-description {
    font-size: 18px;
    margin-bottom: 20px;
    color: #dddddd;
  }
  
  /* üåü Ti√™u ƒë·ªÅ ph·ª• */
  .career-goals-subtitle {
    font-size: 22px;
    font-weight: 600;
    margin-top: 25px;
    color: #00ddff;
    border-bottom: 2px solid rgba(0, 221, 255, 0.6);
    padding-bottom: 5px;
    display: inline-block;
  }
  
  /* üåü Danh s√°ch m·ª•c ti√™u */
  .career-goals-list {
    list-style: none;
    padding: 0;
  }
  
  .career-goals-list li {
    font-size: 18px;
    margin: 8px 0;
    padding: 10px;
    background: rgba(0, 170, 255, 0.2);
    border-radius: 8px;
    display: flex;
    align-items: center;
    gap: 10px;
    transition: all 0.3s ease-in-out;
  }
  
  .career-goals-list li:hover {
    background: rgba(0, 170, 255, 0.4);
    transform: scale(1.05);
  }
  
  /* üåü Hi·ªáu ·ª©ng fadeIn */
  @keyframes fadeIn {
    from {
      opacity: 0;
      transform: translateY(20px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  /* üåü Responsive */
  @media (max-width: 768px) {
    .career-goals-container {
      padding: 15px;
    }
  
    .career-goals-title {
      font-size: 26px;
    }
  
    .career-goals-image img {
      width: 80%;
    }
  
    .career-goals-description {
      font-size: 16px;
    }
  
    .career-goals-list li {
      font-size: 16px;
      padding: 8px;
    }
  }
  