/* Thi·∫øt l·∫≠p t·ªïng th·ªÉ */
.personal-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    min-height: 100vh;
    background: linear-gradient(135deg, #1e1e2f, #2c2c54);
    color: white;
    padding: 20px;
}

/* Avatar Container */
.avatar-container {
    margin-bottom: 20px;
}

.avatar {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    border: 4px solid #00aaff;
    box-shadow: 0px 0px 20px rgba(0, 170, 255, 0.8);
    transition: transform 0.3s ease-in-out;
}

.avatar:hover {
    transform: scale(1.1) rotate(5deg);
}

/* Hi·ªáu ·ª©ng th√¥ng tin */
.info-container, .hobbies-container {
    background: rgba(255, 255, 255, 0.1);
    padding: 20px;
    margin: 10px;
    border-radius: 15px;
    backdrop-filter: blur(10px);
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.3);
    max-width: 500px;
    transition: transform 0.3s ease-in-out;
}

.info-container:hover, .hobbies-container:hover {
    transform: translateY(-5px);
    box-shadow: 0px 0px 20px rgba(0, 170, 255, 0.5);
}

/* Ti√™u ƒë·ªÅ */
.title {
    font-size: 2rem;
    font-weight: bold;
    margin-bottom: 10px;
    color: #00aaff;
}

/* S·ªü th√≠ch */
.subtitle {
    font-size: 1.5rem;
    font-weight: bold;
    color: #00ddff;
}

/* Highlight text */
.highlight {
    color: #ffcc00;
    font-weight: bold;
    transition: color 0.3s ease-in-out;
}

.highlight:hover {
    color: #00ffcc;
}

/* Danh s√°ch s·ªü th√≠ch */
.hobbies-container ul {
    list-style: none;
    padding: 0;
}

.hobbies-container li {
    font-size: 1.2rem;
    padding: 5px;
    background: rgba(0, 170, 255, 0.2);
    border-radius: 5px;
    transition: transform 0.3s ease-in-out;
}

.hobbies-container li:hover {
    transform: scale(1.1);
    background: rgba(0, 170, 255, 0.5);
}
