/* üåü ƒê·ªãnh nghƒ©a bi·∫øn m√†u */
:root {
    --primary-color: #00aaff;
    --background-color: #1a1a2e;
    --navbar-bg: linear-gradient(135deg, #1a1a2e, #16213e);
    --hover-bg: rgba(0, 170, 255, 0.7);
    --shadow-color: rgba(0, 0, 0, 0.5);
  }
  
  /* üåü Navbar */
  .navbar {
    background: var(--navbar-bg);
    color: white;
    padding: 1rem 0;
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
    box-shadow: 0 4px 10px var(--shadow-color);
    z-index: 1000;
    transition: all 0.3s ease-in-out;
  }
  
  /* üåü Container c·ªßa navbar */
  .navbar-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 1.5rem;
  }
  
  /* üåü Logo */
  .navbar-logo {
    font-size: 1.6rem;
    font-weight: bold;
    color: var(--primary-color);
    text-transform: uppercase;
    letter-spacing: 1px;
    cursor: pointer;
    transition: color 0.3s ease-in-out;
  }
  
  .navbar-logo:hover {
    color: #ffcc00;
  }
  
  /* üåü Menu ch√≠nh */
  .navbar-menu {
    display: flex;
    gap: 1.2rem;
    list-style: none;
    margin: 0;
    padding: 0;
  }
  
  /* üåü Li√™n k·∫øt menu */
  .navbar-menu a {
    color: white;
    text-decoration: none;
    font-size: 1rem;
    font-weight: 500;
    padding: 0.5rem 1rem;
    border-radius: 5px;
    transition: all 0.3s ease-in-out;
  }
  
  .navbar-menu a:hover {
    background: var(--hover-bg);
    color: black;
    box-shadow: 0px 4px 10px rgba(0, 170, 255, 0.5);
  }
  
  /* üåü Mobile Menu Icon */
  .menu-icon {
    display: none;
    font-size: 1.8rem;
    cursor: pointer;
    color: white;
  }
  
  /* üåü Responsive Navbar cho Mobile */
  @media (max-width: 768px) {
    .menu-icon {
      display: block;
    }
  
    .navbar-menu {
      display: none;
      flex-direction: column;
      position: absolute;
      top: 60px;
      left: 0;
      background: rgba(26, 26, 46, 0.95);
      width: 100%;
      padding: 0.5rem 0;
      text-align: center;
      box-shadow: 0 4px 10px var(--shadow-color);
    }
  
    .navbar-menu.active {
      display: flex;
    }
  
    .navbar-menu li {
      padding: 0.75rem 0;
    }
  
    .navbar-menu a {
      display: block;
      padding: 0.75rem;
      width: 100%;
    }
  
    .navbar-menu a:hover {
      background: var(--hover-bg);
      color: black;
    }
  }
  
  /* üåü ƒê·∫£m b·∫£o n·ªôi dung kh√¥ng b·ªã che khu·∫•t b·ªüi Navbar */
  body {
    padding-top: 4rem;
  }
  
  .main-content {
    padding-top: 4rem;
  }
  