/* Background ƒë·ªông Sci-Fi */
@keyframes futuristic-bg {
    0% { background-position: 0% 50%; }
    50% { background-position: 100% 50%; }
    100% { background-position: 0% 50%; }
  }
  
  /* Glowing Effect */
  @keyframes glow {
    0% { box-shadow: 0 0 10px #00ffff; }
    50% { box-shadow: 0 0 30px #00ffff; }
    100% { box-shadow: 0 0 10px #00ffff; }
  }
  
  /* Container ch√≠nh */
  .experience-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    min-height: 100vh;
    background: linear-gradient(-45deg, #0f0c29, #302b63, #24243e);
    background-size: 400% 400%;
    animation: futuristic-bg 10s ease infinite;
    color: white;
    padding: 20px;
    border-radius: 20px;
    box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.5);
  }
  
  /* Ti√™u ƒë·ªÅ */
  .title {
    font-size: 3rem;
    font-weight: bold;
    color: #00ffff;
    text-shadow: 0px 0px 20px rgba(0, 255, 255, 0.8);
    margin-bottom: 20px;
  }
  
  /* Danh s√°ch d·ª± √°n */
  .projects-list {
    display: flex;
    flex-direction: column;
    gap: 20px;
    width: 100%;
    max-width: 600px;
  }
  
  /* M·ªói d·ª± √°n */
  .project-item {
    background: rgba(255, 255, 255, 0.1);
    padding: 20px;
    border-radius: 15px;
    backdrop-filter: blur(10px);
    transition: transform 0.3s ease-in-out;
    box-shadow: 0px 4px 20px rgba(0, 255, 255, 0.5);
    animation: glow 2s infinite alternate;
  }
  
  .project-item:hover {
    transform: scale(1.05) rotate(2deg);
  }
  
  /* Ti√™u ƒë·ªÅ d·ª± √°n */
  .project-item h2 {
    font-size: 2rem;
    font-weight: bold;
    color: #ffcc00;
  }
  
  /* Hi·ªáu ·ª©ng ph√°t s√°ng ch·ªØ */
  .project-item p {
    font-size: 1.2rem;
    color: #ffffff;
  }
  
  .project-item a {
    color: #00ffff;
    text-decoration: none;
    font-weight: bold;
  }
  
  .project-item a:hover {
    text-shadow: 0px 0px 10px rgba(0, 255, 255, 0.8);
  }
  