/* N·ªÅn v≈© tr·ª• */
.stars {
    position: absolute;
    width: 100%;
    height: 100%;
    background: black url("https://source.unsplash.com/1600x900/?galaxy,stars") center/cover no-repeat;
    z-index: -1;
    overflow: hidden;
  }
  
  /* ƒê·∫£m b·∫£o n·ªÅn sao ph·ªß to√†n b·ªô m√†n h√¨nh */
.stars {
    position: fixed;
    top: 0;
    left: 0;
    width: 200vw;  /* TƒÉng k√≠ch th∆∞·ªõc ƒë·ªÉ ph·ªß to√†n m√†n h√¨nh */
    height: 200vh;
    background: black url("https://source.unsplash.com/1600x900/?galaxy,stars") repeat;
    background-size: cover;
    animation: moveStars 60s linear infinite;
    z-index: -1;
}

/* Hi·ªáu ·ª©ng di chuy·ªÉn v√¥ t·∫≠n */
@keyframes moveStars {
    from {
        transform: translate(0, 0);
    }
    to {
        transform: translate(-50vw, -50vh);
    }
}

/* L·ªõp sao nh·ªè h∆°n, t·∫°o chi·ªÅu s√¢u */
.stars::before, .stars::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 200%;
    height: 200%;
    background: transparent url("https://www.transparenttextures.com/patterns/stardust.png") repeat;
    animation: moveStarsSecondary 80s linear infinite;
}

/* Hi·ªáu ·ª©ng di chuy·ªÉn nh·∫π h∆°n cho l·ªõp sao th·ª© hai */
@keyframes moveStarsSecondary {
    from {
