/* Background Sci-Fi ƒë·ªông */
@keyframes futuristic-bg {
    0% { background-position: 0% 50%; }
    50% { background-position: 100% 50%; }
    100% { background-position: 0% 50%; }
  }
  
  /* Glowing Effect */
  @keyframes glow {
    0% { box-shadow: 0 0 10px #00ffff; }
    50% { box-shadow: 0 0 30px #00ffff; }
    100% { box-shadow: 0 0 10px #00ffff; }
  }
  
  /* Container ch√≠nh */
  .skills-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    min-height: 100vh;
    background: linear-gradient(-45deg, #0f0c29, #302b63, #24243e);
    background-size: 400% 400%;
    animation: futuristic-bg 10s ease infinite;
    color: white;
    padding: 20px;
    border-radius: 20px;
    box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.5);
  }
  
  /* Ti√™u ƒë·ªÅ */
  .title {
    font-size: 3rem;
    font-weight: bold;
    color: #00ffff;
    text-shadow: 0px 0px 20px rgba(0, 255, 255, 0.8);
    margin-bottom: 20px;
  }
  
  /* Danh s√°ch k·ªπ nƒÉng */
  .skills-list {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    gap: 20px;
    width: 100%;
    max-width: 800px;
    justify-items: center;
  }
  
  /* M·ªói k·ªπ nƒÉng */
  .skill-item {
    width: 120px;
    height: 120px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: rgba(255, 255, 255, 0.1);
    padding: 15px;
    border-radius: 15px;
    backdrop-filter: blur(10px);
    transition: transform 0.3s ease-in-out;
    box-shadow: 0px 4px 20px rgba(0, 255, 255, 0.5);
    animation: glow 2s infinite alternate;
  }
  
  .skill-item:hover {
    transform: scale(1.1);
  }
  
  /* T√™n k·ªπ nƒÉng */
  .skill-item p {
    margin-top: 10px;
    font-size: 1.2rem;
    color: #ffffff;
  }
  